@charset "utf-8";

* {
  margin: 0;
  padding: 0;
}

:root {
  --dominantcolor: white;
  --assortcolor: black;
  --accentcolor: #0df;
}

.header h1 {
background-color: var(--assortcolor);
color: var(--accentcolor);
text-align: center;
height: 2em;
line-height: 2;
}

section {
  margin-top: 2em;
}

h2 {
  height: 3em;
  text-align: center;
  color: var(--accentcolor);
}

article {
  margin-left: 20px;
  margin-bottom: 2em;
}

h3 {
  margin-bottom: 1em;
}

ol, ul {
  padding-left: 30px;
}

caption {
  text-align: left;
  margin-bottom: 0.5em;
}

table {
  border-collapse: collapse;
  border-spacing: none;
  width: 100%;
}

th, td {
  border: 1px solid var(--assortcolor);
  padding: 1em;
}

thead th {
  background-color: deepskyblue;
  color: var(--dominantcolor);
}

tbody > tr:nth-child(1) td, tbody > tr:nth-child(6) td, tbody > tr:nth-child(7) td {
text-align: center;
}